import {memo} from 'react';
import {NewsType} from "./News";

type NewsListPropsType = {
    newsList: NewsType
}

export default memo(function NewsList({newsList}: NewsListPropsType) {
    return (
        <>

            <h1>  {newsList.updatedDate}</h1>
            <h3> </h3>
            {newsList.types.map((n) => {
                return <ul>
                    <li>{n.type}</li>
                </ul>
            })}
        </>
    );
})